.header {
  background-image: url(../images/photos/header.png);
  background-size: cover;
  background-position: right 13% center;
  background-repeat: no-repeat;

  &__content {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    padding: $headerTopPadding 0 88px;

    @include onTablet(){
      padding-bottom: 130px;
    }

    @include onDesktop(){
      padding-bottom: 200px;
    }
  }

  &__icons{
    display: flex;
    align-items: center;
  }


  &__call {

    @include onDesktop(){
      text-decoration: none;

        &::before {
          content: "+1 234 555-55-55";
          display: block;
          position: relative;
          top: 50%;
          transform: translate(-100%, -50%);
          width: max-content;

          color: #fff;
          font-weight: 700;
          font-size: 12px;
          line-height: 16px;
          text-align: right;
          letter-spacing: 2px;

          padding-right: 12px;
          opacity: 0;
          pointer-events: none;
        }

        &:hover::before {
          opacity: 1;
          pointer-events: all;
        }
      }
    }

  &__menu--opener {

      @include onDesktop(){
        &:hover {
          background-image: url(../images/hoverMenu.svg);
        }
      }
    }

  &__title {
    margin: 0;
    font-weight: 700;
    color: white;
    font-size: 36px;
    letter-spacing: -1.5px;

    text-align: center;

    @include onTablet(){
      font-size: 64px;
    }

    @include onDesktop(){
      font-size: 80px;
    }
  }
}
